<template>
  <section class="pc-container pb-4">
    <div class="pcoded-content">
      <div class="row mb-4 d-flex justify-content-between">
        <div class="col-sm-12 col-md-6 col-xl-9">
          <h5 class="text-dark fw-bold">
            <button class="btn btn-sm mr-2 btn-primary" @click="goBack">
              <i data-feather="arrow-left"></i>
            </button>Busqueda de expedientes administrativos
          </h5>
        </div>
        <div class="col-sm-12 col-md-6 col-xl-3">
          <select class="form-control" v-model="anio" @change="mostrarTradesAnio()">
            <option v-for="(anio, index) in anios" :key="index" :value="anio" class="py-3">
              {{ anio }}
            </option>
          </select>
        </div>
      </div>
      <div class="row">
        <div class="col-xl-12">
          <div class="card shadow">
            <div class="card-header">
              <h5 class="text-secondary">
                Lista de expedientes
              </h5>
            </div>
            <div class="card-body">
              <div class="table-responsive">
                <table class="table" id="table" ref="dataTable">
                  <thead>
                    <tr>
                      <th>N° de expediente</th>
                      <th>Nombre</th>
                      <th>Asunto</th>
                      <th>Fecha</th>
                      <th>Responsable</th>
                      <th>Área de origen</th>
                      <th>Tipo de referencia</th>
                      <th>Estado</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="trade in tradesAnio" :key="trade.tra_id">
                      <td>
                        <router-link :to="{ name: 'oficio/detalle', params: { id: trade.tra_id } }">
                          {{ trade.tra_number }}
                        </router-link>
                      </td>
                      <td>{{ trade.tra_name }}</td>
                      <td>{{ trade.tra_matter }}</td>
                      <td>{{ trade.tra_arrival_date }}</td>
                      <td>{{ trade.lawyer.persona.nat_nombres }} {{
                        trade.lawyer.persona.nat_apellido_paterno }} {{ trade.lawyer.persona.nat_apellido_materno }}
                      </td>
                      <td>{{ trade.area.are_name }}</td>
                      <td>{{ trade.type_reference.type_name }}</td>
                      <td v-if="trade.tra_state_law === 'P'" class="pr-5">
                        <span :class="badgeClassOficio(trade.tra_state_law)">
                          Pendiente
                        </span>
                      </td>
                      <td v-if="trade.tra_state_law === 'F'" class="pr-5">
                        <span :class="badgeClassOficio(trade.tra_state_law)">
                          Terminado
                        </span>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { badgeClassOficio } from '@/utils/utils.js';
import feather from 'feather-icons';
import DataTable from '@/utils/dataTable';
import { badgeClass } from '@/utils/utils.js';
export default {
  name: 'Buscar',
  mounted() {
    this.$nextTick(() => {
      feather.replace();
    });
    this.cargarAños();
    this.cargarTrades();
    
  },

  data() {
    return {
      anio: "",
      anios: [],
      trades: [],
      dataTable: null,
      exp: {},
      idTrade: null,
      showModal: "",
      tradesAnio: [],
    };
  },

  computed: {
    mostrarTradesAnio() {
      const numAnio = parseInt(this.anio);
      this.tradesAnio = [];
      this.tradesAnio = this.trades.filter(trade => trade.anio === numAnio);
      this.initDataTable();
      return this.tradesAnio;
    }
  },

  methods: {
    async cargarAños(){
      this.$showLoadingAlert();
      const anioActual = new Date().getFullYear();
      for (let i = anioActual; i >= 2018; i--) {
        this.anios.push(i);
      }
      this.anio = this.anios[0];
    },
    async cargarTrades() {
      try {
        const result = await this.$getData('trade', this);
        this.trades = result.data;
        this.tradesAnio = this.trades.filter(trade => trade.anio === this.anio);
        this.initDataTable();
        this.$closeLoadingAlert();
        
      } catch (error) {
        this.$handleApiError(error, 'cargar oficios');
      }
    },

    
    async initDataTable() {
      this.destroyDataTable();
      await this.$nextTick();
      this.dataTable = $(this.$refs.dataTable).DataTable({
        language: {
          url: '//cdn.datatables.net/plug-ins/1.13.6/i18n/es-ES.json',
        },
      });
    },
    
    destroyDataTable() {
      this.dataTable && this.dataTable.destroy() && (this.dataTable = null);
    },

    goBack() {
      if (this.$store.getters.get__user.usu_rol === 'ABOGADO') {
        this.$router.push({ path: '/oficio/asignados' });
      } else {
        this.$router.go(-1);
      }
    },
    badgeClassOficio,
  },
};
</script>

<style scoped>
@import '@/assets/css/fileStatus.css';
@import '@/assets/css/dataTable.css';

.container-div {
  position: relative;
}

.content-events {
  position: absolute;
  bottom: 5em;
  right: 5em;
  background-color: #fff;
  padding: 10px;
  border: 1px solid #ccc;
  z-index: 1;
}

.bg-links {
  background: #e3e1fc;
  color: #7267EF;
  border: 1px solid #e3e1fc;
  margin: 2.5px !important;
}
</style>